.elements__list {
  list-style: none;
  padding: 0;
  margin: 0;
}

@media (min-width: 768px) {
  .elements__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 18px;
    row-gap: 20px;
  }
}

@media (min-width: 1024px) {
  .elements__list {
    grid-template-columns: repeat(3, 1fr);
  }
}
